import uniCollapse from '@/components/uni-collapse/uni-collapse.vue'
import uniCollapseItem from '@/components/uni-collapse-item/uni-collapse-item.vue'
import uniSection from '@/components/uni-section/uni-section.vue'
import uniIcons from '@/components/uni-icons/uni-icons.vue'
import uniCard from '@/components/uni-card/uni-card.vue'
import {
	mapState
} from 'vuex';


export default {
	
	data() {
		return {
			Rlist: [],
			Blist: [],
			Slist: [],
		}
	},
	onLoad: function() {
		var _self = this
		//获取轮播图
		uni.request({
			url:'http://192.168.31.8:8081/getArticle',
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			data: {
			},
			success: res => {
				console.log(res);
				console.log(res.data.data.length);
				_self.Slist = []
				for(let i = 0; i < res.data.data.length; i++){
					_self.Slist.push(res.data.data[i])
				}
				
			},
		})


	},

	computed: {
		...mapState(['hasLogin', 'userInfo', 'skey']),
		isdisablecol: function() {
			return !this.hasLogin
		},
		getTime: function() {
			var date = new Date(),
				year = date.getFullYear(),
				month = date.getMonth() + 1,
				day = date.getDate(),
				hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours(),
				minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes(),
				second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
			month >= 1 && month <= 9 ? (month = "0" + month) : "";
			day >= 0 && day <= 9 ? (day = "0" + day) : "";
			var timer = year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second;
			var timer1 = year + '-' + month + '-' + day;
			console.log("time1" + timer);
			console.log("date" + date);
			return timer1;
		},


	},

	

	onShow() {
		// 未登录
		if (!this.hasLogin) {
			uni.showModal({
				title: "未登录",
				content: "是否现在登录?",
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						uni.navigateTo({
							url: "../wxlogin/wxlogin"
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			})
		}
		//已登录
		else{
			var _self = this;
			_self.$options.methods.relist(_self);
		}
	},
	
	methods: {
		confirm:function(e){
		    var that=this;
		    var discountName=e.detail.value['search - input'] ?e.detail.value['search - input'] : e.detail.value 
		    console.log('e.detail.value', discountName)
			that.$options.methods.searchBook(discountName)
		  },
		confirmbut(res){
			var that=this;
			console.log(res.detail.value.content);
			that.$options.methods.searchBook(res.detail.value.content)
			},
		searchBook(content){
			console.log("search",content);
			var text = JSON.stringify(content);
			uni.navigateTo({
				url: '../search_result/search_result?content='+ encodeURIComponent(text),
			})
			console.log("aa");
		},
		relist(_self){
			uni.request({
				
				url:'http://192.168.31.8:8081/getBorrowingRecord',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					skey: _self.skey
				},
				success: res => {
					console.log(res);
					console.log(res.data.length);
					_self.Rlist = []
					for(let i = 0; i < res.data.length; i++){
						_self.Rlist.push(res.data[i])
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},

		change() {
			// 未登录
			if (!this.hasLogin) {
				uni.showModal({
					title: "未登录",
					content: "是否现在登录?",
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
							uni.navigateTo({
								url: "../wxlogin/wxlogin"
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
				return
			} else {
				// 已经登录，加载数据
				uni.showLoading({
					title: "Loding---"
				})
				var _self = this;
				_self.$options.methods.relist(_self);
				
				uni.hideLoading()
			}
		},
		click(e) {
			console.log(e);
			uni.navigateTo({
				url: '../article/article?articleId='+e
			})
		},
		click1() {
			console.log("click1!");
		},

		toLogin() {
			console.log(this.hasLogin);
			if (!this.hasLogin) {
				uni.switchTab({
					url: '../user/user'
				});
			}
		},

		formSubmit(isbn) {
			var _self = this;
			uni.showLoading()
			uni.request({
				url: 'http://192.168.31.8:8081/renew',
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					isbn: isbn,
					skey: this.skey
					
				},
				success(res) {
					if (res != null) {
						console.log("成功");
						console.log(res);
						uni.showToast({
							duration:2000
						})
					} else {
						console.log('服务器异常')
					}
				},
				fail(error) {
					console.log(error)
				},
				complete() {
					_self.$options.methods.relist(_self);
					uni.hideLoading()
				}
			})
		}

	}
}
